import React, { useState, useEffect } from 'react';

// Material-UI components
import Box from '@material-ui/core/Box';

const colors = {
    //available: '#eee',
    // used: 'lightgreen',
    // wrong: 'lightcoral',
    selected: '#3008F7',
    unselected: 'rgba(211,211,211,0.87)'
};

const LessonBoard = (props) => {

    const [activeItem, setActiveItem] = useState(-1);
    let [lesson, setLesson] = useState(props.lesson);


    const handleClick = index => {
        setActiveItem(index);
    };

    useEffect(() => {
        setLesson(props.lesson);
    });

    return (
        <Box container display="flex" justifyContent="center" minHeight="70vh" alignItems="center" boxShadow={1} margin="1em">
            <Box display="flex" flexWrap="wrap" justifyContent="center">
                {lesson.map((word, index) => {
                    let syllabel = word.split("-");

                    if (syllabel.length > 1) {
                        return syllabel.map((vowel, index1) => (
                            <div
                                key={index.toString() + index1.toString()}
                                style={{
                                    color:
                                        activeItem === index.toString() + index1.toString()
                                            ? colors['selected'] : colors['unselected'],
                                    paddingRight: index1 === (syllabel.length - 1) ? '0.25em' : "0em",
                                    fontSize: '5rem'
                                }}
                                onClick={handleClick.bind(
                                    this,
                                    index.toString() + index1.toString()
                                )}
                            >
                                {vowel}
                            </div>
                        ));


                    } else {
                        return syllabel.map(vowel => (
                            <div
                                key={index}
                                style={{
                                    paddingRight: '0.25em',
                                    fontSize: '5rem',
                                    color:
                                        activeItem === index ? colors['selected'] : colors['unselected'],
                                }}
                                onClick={handleClick.bind(this, index)}
                            >
                                {vowel}
                            </div>
                        ));
                    }
                })}
            </Box>
        </Box>
    );
};

export default LessonBoard;