import React from 'react';
import Layout from '../../components/Layout';
import LessonBoard from '../../components/LessonBoard';

import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
        },
    },
}));

const Lesson = (props) => {
    const classes = useStyles();
    const { onSelectedLesson} = props;
    return (
        <Layout onSelectedLesson={onSelectedLesson}>
            <Box container display="row" justifyContent="center" minHeight="80vh" alignItems="center" padding={1} boxShadow={10}>
                <LessonBoard lesson={props.lesson} />
                <Box container display="flex" justifyContent="center" alignItems="center">
                    <div className={classes.root}>
                        <Button onClick={props.onSelectedSyllabel} variant="contained" color="default">
                            Sílabas</Button>
                        <Button variant="contained" color="primary" onClick={props.onSelectedSentence}>ORACIONES</Button>
                    </div>
                </Box>
            </Box>
        </Layout>
    );
}

export default Lesson;